"use client";

import { useCallback, useState } from "react";
import JSZip from "jszip";

interface GeneratedAudio {
  text: string;
  audioBlob: Blob;
  audioUrl: string;
  index: number;
}

interface DownloadManagerProps {
  generatedAudios: GeneratedAudio[];
}

export default function DownloadManager({ generatedAudios }: DownloadManagerProps) {
  const [isCreatingZip, setIsCreatingZip] = useState(false);

  const convertToMp3 = useCallback(async (audioBlob: Blob): Promise<Blob> => {
    // Trong th·ª±c t·∫ø, b·∫°n c√≥ th·ªÉ s·ª≠ d·ª•ng lamejs ƒë·ªÉ chuy·ªÉn ƒë·ªïi sang MP3
    // Hi·ªán t·∫°i ch√∫ng ta s·∫Ω tr·∫£ v·ªÅ blob g·ªëc
    return audioBlob;
  }, []);

  const downloadSingleFile = useCallback(async (audio: GeneratedAudio) => {
    try {
      const mp3Blob = await convertToMp3(audio.audioBlob);
      const url = URL.createObjectURL(mp3Blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `voice_clone_${audio.index + 1}.mp3`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
    } catch (error) {
      console.error('L·ªói khi t·∫£i file:', error);
      alert('C√≥ l·ªói x·∫£y ra khi t·∫£i file!');
    }
  }, [convertToMp3]);

  const downloadAllAsZip = useCallback(async () => {
    if (generatedAudios.length === 0) return;

    setIsCreatingZip(true);
    try {
      const zip = new JSZip();
      
      // Th√™m t·ª´ng file v√†o ZIP
      for (let i = 0; i < generatedAudios.length; i++) {
        const audio = generatedAudios[i];
        const mp3Blob = await convertToMp3(audio.audioBlob);
        const fileName = `voice_clone_${audio.index + 1}.mp3`;
        zip.file(fileName, mp3Blob);
      }

      // T·∫°o file README.txt v·ªõi th√¥ng tin
      const readmeContent = `·ª®ng D·ª•ng Nh√¢n B·∫£n Gi·ªçng N√≥i
=================================

T·ªïng s·ªë file: ${generatedAudios.length}
Ng√†y t·∫°o: ${new Date().toLocaleString('vi-VN')}

Danh s√°ch file:
${generatedAudios.map((audio, index) => 
  `${index + 1}. voice_clone_${audio.index + 1}.mp3 - "${audio.text.substring(0, 50)}${audio.text.length > 50 ? '...' : ''}"`
).join('\n')}

C·∫£m ∆°n b·∫°n ƒë√£ s·ª≠ d·ª•ng ·ª©ng d·ª•ng!
`;
      
      zip.file("README.txt", readmeContent);

      // T·∫°o v√† t·∫£i ZIP
      const zipBlob = await zip.generateAsync({ type: "blob" });
      const url = URL.createObjectURL(zipBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `voice_clones_${new Date().toISOString().split('T')[0]}.zip`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
    } catch (error) {
      console.error('L·ªói khi t·∫°o ZIP:', error);
      alert('C√≥ l·ªói x·∫£y ra khi t·∫°o file ZIP!');
    } finally {
      setIsCreatingZip(false);
    }
  }, [generatedAudios, convertToMp3]);

  const getTotalSize = useCallback(() => {
    const totalBytes = generatedAudios.reduce((sum, audio) => sum + audio.audioBlob.size, 0);
    const mb = (totalBytes / (1024 * 1024)).toFixed(2);
    return `${mb} MB`;
  }, [generatedAudios]);

  if (generatedAudios.length === 0) {
    return null;
  }

  return (
    <div className="mt-6 space-y-4">
      <div className="border-t border-gray-200 pt-4">
        <h3 className="text-lg font-semibold text-gray-800 mb-4">
          üì• T·∫£i xu·ªëng file
        </h3>
        
        {/* Download Statistics */}
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4 mb-4">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-center">
            <div>
              <div className="text-2xl font-bold text-blue-600">{generatedAudios.length}</div>
              <div className="text-sm text-blue-800">File MP3</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-600">{getTotalSize()}</div>
              <div className="text-sm text-blue-800">T·ªïng dung l∆∞·ª£ng</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-600">
                {Math.round(generatedAudios.reduce((sum, audio) => {
                  // ∆Ø·ªõc t√≠nh th·ªùi l∆∞·ª£ng d·ª±a tr√™n k√≠ch th∆∞·ªõc file
                  return sum + (audio.audioBlob.size / 16000); // Gi·∫£ s·ª≠ 16KB/gi√¢y
                }, 0))}s
              </div>
              <div className="text-sm text-blue-800">T·ªïng th·ªùi l∆∞·ª£ng</div>
            </div>
            <div>
              <div className="text-2xl font-bold text-blue-600">MP3</div>
              <div className="text-sm text-blue-800">ƒê·ªãnh d·∫°ng</div>
            </div>
          </div>
        </div>

        {/* Download Options */}
        <div className="space-y-3">
          {/* Download All as ZIP */}
          <button
            onClick={downloadAllAsZip}
            disabled={isCreatingZip}
            className={`w-full flex items-center justify-center gap-2 px-6 py-3 rounded-lg font-semibold transition-all duration-200 ${
              isCreatingZip
                ? 'bg-gray-400 cursor-not-allowed text-white'
                : 'bg-green-600 hover:bg-green-700 text-white hover:scale-105'
            }`}
          >
            {isCreatingZip ? (
              <>
                <div className="animate-spin rounded-full h-4 w-4 border-2 border-white border-t-transparent"></div>
                ƒêang t·∫°o file ZIP...
              </>
            ) : (
              <>
                <span className="text-lg">üì¶</span>
                T·∫£i t·∫•t c·∫£ ({generatedAudios.length} file) - ZIP
              </>
            )}
          </button>

          {/* Individual Downloads */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-2">
            {generatedAudios.map((audio, index) => (
              <button
                key={index}
                onClick={() => downloadSingleFile(audio)}
                className="flex items-center gap-2 px-4 py-2 bg-blue-100 hover:bg-blue-200 text-blue-800 rounded-lg transition-colors text-sm"
              >
                <span>üíæ</span>
                <span className="truncate">
                  File {audio.index + 1}: {audio.text.substring(0, 30)}
                  {audio.text.length > 30 ? '...' : ''}
                </span>
              </button>
            ))}
          </div>
        </div>

        {/* Download Tips */}
        <div className="mt-4 bg-yellow-50 border border-yellow-200 rounded-lg p-3">
          <div className="text-sm text-yellow-800">
            <strong>üí° L∆∞u √Ω:</strong>
            <ul className="mt-1 space-y-1 text-xs">
              <li>‚Ä¢ File ZIP ch·ª©a t·∫•t c·∫£ MP3 + file README v·ªõi th√¥ng tin chi ti·∫øt</li>
              <li>‚Ä¢ C√≥ th·ªÉ t·∫£i t·ª´ng file ri√™ng l·∫ª n·∫øu ch·ªâ c·∫ßn m·ªôt s·ªë file c·ª• th·ªÉ</li>
              <li>‚Ä¢ File ƒë∆∞·ª£c ƒë·∫∑t t√™n theo th·ª© t·ª±: voice_clone_1.mp3, voice_clone_2.mp3...</li>
              <li>‚Ä¢ ƒê·ªãnh d·∫°ng MP3 t∆∞∆°ng th√≠ch v·ªõi h·∫ßu h·∫øt thi·∫øt b·ªã v√† ·ª©ng d·ª•ng</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
}
