"use client";

import { useState, useRef, useCallback } from "react";
import AudioUploader from "./AudioUploader";
import TextProcessor from "./TextProcessor";
import VoiceControls from "./VoiceControls";
import AudioPlayer from "./AudioPlayer";
import DownloadManager from "./DownloadManager";

interface AudioFile {
  file: File;
  url: string;
  name: string;
}

interface GeneratedAudio {
  text: string;
  audioBlob: Blob;
  audioUrl: string;
  index: number;
}

export default function VoiceCloner() {
  const [sampleAudio, setSampleAudio] = useState<AudioFile | null>(null);
  const [inputText, setInputText] = useState("");
  const [textLines, setTextLines] = useState<string[]>([]);
  const [speed, setSpeed] = useState(1.0);
  const [isProcessing, setIsProcessing] = useState(false);
  const [generatedAudios, setGeneratedAudios] = useState<GeneratedAudio[]>([]);
  const [currentPlayingIndex, setCurrentPlayingIndex] = useState<number | null>(null);

  const handleAudioUpload = useCallback((file: File) => {
    const url = URL.createObjectURL(file);
    setSampleAudio({
      file,
      url,
      name: file.name
    });
  }, []);

  const handleTextChange = useCallback((text: string) => {
    setInputText(text);
    const lines = text.split('\n').filter(line => line.trim() !== '');
    setTextLines(lines);
  }, []);

  const handleSpeedChange = useCallback((newSpeed: number) => {
    setSpeed(newSpeed);
  }, []);

  const generateVoiceClones = useCallback(async () => {
    if (!sampleAudio || textLines.length === 0) {
      alert("Vui l√≤ng t·∫£i l√™n file √¢m thanh m·∫´u v√† nh·∫≠p vƒÉn b·∫£n!");
      return;
    }

    setIsProcessing(true);
    setGeneratedAudios([]);

    try {
      const audioContext = new AudioContext();
      const arrayBuffer = await sampleAudio.file.arrayBuffer();
      const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);

      const generatedList: GeneratedAudio[] = [];

      for (let i = 0; i < textLines.length; i++) {
        const text = textLines[i];
        
        // S·ª≠ d·ª•ng Web Speech API ƒë·ªÉ t·∫°o gi·ªçng n√≥i
        const utterance = new SpeechSynthesisUtterance(text);
        utterance.rate = speed;
        utterance.pitch = 1.0;
        utterance.volume = 1.0;

        // T√¨m gi·ªçng n√≥i ti·∫øng Vi·ªát n·∫øu c√≥
        const voices = speechSynthesis.getVoices();
        const vietnameseVoice = voices.find(voice => 
          voice.lang.includes('vi') || voice.lang.includes('VN')
        );
        if (vietnameseVoice) {
          utterance.voice = vietnameseVoice;
        }

        // T·∫°o audio blob t·ª´ speech synthesis
        const audioBlob = await new Promise<Blob>((resolve) => {
          const mediaRecorder = new MediaRecorder(new MediaStream());
          const chunks: BlobPart[] = [];

          mediaRecorder.ondataavailable = (event) => {
            chunks.push(event.data);
          };

          mediaRecorder.onstop = () => {
            const blob = new Blob(chunks, { type: 'audio/wav' });
            resolve(blob);
          };

          // B·∫Øt ƒë·∫ßu ghi √¢m v√† ph√°t speech
          mediaRecorder.start();
          speechSynthesis.speak(utterance);

          utterance.onend = () => {
            setTimeout(() => {
              mediaRecorder.stop();
            }, 100);
          };
        });

        const audioUrl = URL.createObjectURL(audioBlob);
        
        generatedList.push({
          text,
          audioBlob,
          audioUrl,
          index: i
        });
      }

      setGeneratedAudios(generatedList);
    } catch (error) {
      console.error("L·ªói khi t·∫°o gi·ªçng n√≥i:", error);
      alert("C√≥ l·ªói x·∫£y ra khi t·∫°o gi·ªçng n√≥i. Vui l√≤ng th·ª≠ l·∫°i!");
    } finally {
      setIsProcessing(false);
    }
  }, [sampleAudio, textLines, speed]);

  const handlePlayAudio = useCallback((index: number) => {
    setCurrentPlayingIndex(index);
  }, []);

  const handleStopAudio = useCallback(() => {
    setCurrentPlayingIndex(null);
  }, []);

  const formatFileSize = useCallback((bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  }, []);

  const downloadSingleFile = useCallback(async (audio: GeneratedAudio) => {
    try {
      const url = URL.createObjectURL(audio.audioBlob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `voice_clone_${audio.index + 1}.mp3`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);
    } catch (error) {
      console.error('L·ªói khi t·∫£i file:', error);
      alert('C√≥ l·ªói x·∫£y ra khi t·∫£i file!');
    }
  }, []);

  return (
    <div className="space-y-6">
      {/* Text Input */}
      <div className="bg-white rounded-lg border border-gray-200 p-6 shadow-sm">
        <TextProcessor 
          text={inputText}
          onTextChange={handleTextChange}
          lines={textLines}
        />
      </div>

      {/* Generate Button */}
      <div className="text-center">
        <button
          onClick={generateVoiceClones}
          disabled={isProcessing || textLines.length === 0}
          className={`px-8 py-3 rounded-lg font-semibold transition-all duration-200 ${
            isProcessing || textLines.length === 0
              ? 'bg-gray-400 cursor-not-allowed text-white'
              : 'bg-green-600 hover:bg-green-700 text-white'
          }`}
        >
          {isProcessing ? (
            <span className="flex items-center gap-2">
              <div className="animate-spin rounded-full h-4 w-4 border-2 border-white border-t-transparent"></div>
              ƒêang x·ª≠ l√Ω...
            </span>
          ) : (
            '‚ñ∂ T·∫°o Gi·ªçng N√≥i'
          )}
        </button>
      </div>

      {/* Generated Audio Results */}
      {generatedAudios.length > 0 && (
        <div className="bg-white rounded-lg border border-gray-200 p-6 shadow-sm">
          <div className="space-y-4">
            {generatedAudios.map((audio, index) => (
              <div key={index} className="border border-gray-100 rounded-lg p-4">
                <div className="flex items-center justify-between mb-3">
                  <span className="text-gray-700 font-medium">
                    {audio.text.substring(0, 50)}
                    {audio.text.length > 50 ? '...' : ''}
                  </span>
                  <span className="text-xs text-gray-500">
                    {formatFileSize(audio.audioBlob.size)}
                  </span>
                </div>
                <div className="flex items-center gap-4">
                  <AudioPlayer
                    audioUrl={audio.audioUrl}
                    isPlaying={currentPlayingIndex === index}
                    onPlay={() => handlePlayAudio(index)}
                    onStop={handleStopAudio}
                  />
                  <button
                    onClick={() => downloadSingleFile(audio)}
                    className="px-4 py-2 bg-green-600 text-white rounded-lg hover:bg-green-700 transition-colors text-sm"
                  >
                    üì• T·∫£i MP3
                  </button>
                </div>
              </div>
            ))}
          </div>
          
          <DownloadManager generatedAudios={generatedAudios} />
        </div>
      )}
    </div>
  );
}
